<template>
  <div id="app">
    <div align="left">
      <img src="./assets/logo.png" width="22" height="22" />
      <font size="5">Vue框架下一体化项目可视化测试页面</font>
    </div>

    <el-drawer title="消息中心" :visible.sync="drawer" :with-header="false">
      <span>这是一个静态页面，可以进一步链接数据库填充消息数据</span>
    </el-drawer>

    <el-dialog title="提示" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
      <span>一体化项目前端框架：点击确定前往查看Vue渐进式JS框架</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="hreftwo">确 定</el-button>
      </span>
    </el-dialog>

    <el-menu
      :default-active="activeIndex2"
      class="el-menu-demo"
      mode="horizontal"
      @select="handleSelect"
      background-color="#545c64"
      text-color="#fff"
      active-text-color="#ffd04b"
    >
      <el-menu-item @click="gaikuang" index="1">项目概况</el-menu-item>
      <el-submenu index="2">
        <template slot="title">热藏动态</template>
        <el-menu-item index="2-1">生产计划</el-menu-item>
        <el-menu-item index="2-2">生产动态</el-menu-item>
        <el-menu-item index="2-3">产量波动</el-menu-item>
        <el-submenu index="2-4">
          <template slot="title">数据分析</template>
          <el-menu-item index="2-4-1">沿线温度</el-menu-item>
          <el-menu-item index="2-4-2">沿线压力</el-menu-item>
          <el-menu-item index="2-4-3">热损失计算</el-menu-item>
        </el-submenu>
      </el-submenu>
      <el-menu-item @click="drawer = true" index="3">消息中心</el-menu-item>
      <el-menu-item @click="dialogVisible = true" index="4">
        <a target="_blank">关于</a>
      </el-menu-item>
    </el-menu>
    <router-view />
  </div>
</template>


<script>

export default {
  name: 'App',



data() {
      return {
        drawer: false,
        dialogVisible: false
      };
    },/*这个是那个侧边栏的按钮事件  第二个是对话框弹出 竟然相安无事*/

methods:{ //跳转页面
hreftwo(){
  
this.dialogVisible= false
window.open('https://cn.vuejs.org/')
},

gaikuang: function() {
      this.$router.push("/gaikuang");
    }

},



}
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 5px; /*js注释 这里确定了与上面页面的距离*/
}
</style>



