import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/gaikuang',
      name: 'Know',
      meta: {
        title: '了解'
      },
        component: (resolve) => require(['@/components/2HelloWorld.vue'], resolve)

    }
  ]
})



