          <el-table-column prop="name" label="井名" width="150"></el-table-column>
          <el-table-column prop="address" label="产水量" width="150"></el-table-column>
          <el-table-column prop="temperature" label="水温"></el-table-column>
        </el-table>

        <p>进一步的，需要读取数据库中数据展示，并丰富展示效果</p>
